export class ZoneDSTList {
    data: ZoneDST[]
}

export class DST {
	id: number;
	nom: string;
	nom_regle: string;
	methode_on: string;
	methode_off: string;	
	debut_on: number;
	debut_off: number;
	fin_on: number;
	fin_off: number;	
	operateur_on: string;
	operateur_off: string;
	decile_on: number;
	decile_off: number;
	jour_on: number;
	mois_off: number;
	mois_on: number;
	jour_off: number;
	heures_on: number;
	heures_off: number;
	constructor(aId: number, aNom: string, aNom_regle: string, aMethode_on: string, aMethode_off: string, aDebut_on: number, aDebut_off: number, aFin_on: number, aFin_off: number, aOperateur_on: string, aOperateur_off: string, aDecile_on: number, aDecile_off: number, aJour_on: number, aMois_off: number, aMois_on: number, aJour_off: number, aHeures_on: number, ahHures_off: number) {
		this.id = aId;
		this.nom = aNom;
		this.nom_regle = aNom_regle;
		this.methode_on = aMethode_on;
		this.methode_off = aMethode_off;	
		this.debut_on = aDebut_on;
		this.debut_off = aDebut_off;
		this.fin_on = aFin_on;
		this.fin_off = aFin_off;
		this.operateur_on = aOperateur_on;
		this.operateur_off = aOperateur_off;
		this.decile_on = aDecile_on;
		this.decile_off = aDecile_off;
		this.jour_on = aJour_on;
		this.mois_off = aMois_off;
		this.mois_on = aMois_on;
		this.jour_off = aJour_off;
		this.heures_on = aHeures_on;
		this.heures_off = ahHures_off;								
	};
}

export class ZoneDST {
	id: number;
	nom: string;
	dst: DST;
	id_iso_3166_2: string; 
	indicatif_tel: string;
	sens: number;
	minutes: number;
	heures: number;
	constructor(aId: number, aNom: string, aDst: DST, aId_iso_3166_2: string, aIndicatif_tel: string, aSens: number, aMinutes: number, aHeures: number) {
		this.id = aId;
		this.nom = aNom;
		this.dst = aDst;
		this.id_iso_3166_2 = aId_iso_3166_2;
		this.indicatif_tel = aIndicatif_tel;
		this.sens = aSens;
		this.minutes = aMinutes;
		this.heures = aHeures;								
	};
}

