import { Component, OnInit , Input} from '@angular/core';
import { FormControl, FormGroup, Validators, FormBuilder } from '@angular/forms';
import {DateDST} from '../zonedstdata';


@Component({
  selector: 'app-dst',
  templateUrl: './dst.component.html',
  styleUrls: ['./dst.component.css']
})
export class DstComponent implements OnInit {
  
  public dateReferenceDST: DateDST;
  public dateRechercheDST: DateDST;

  constructor() { }

  ngOnInit() {

    var dateNowArrondi: Date = new Date();
    dateNowArrondi.setMinutes(0);
    dateNowArrondi.setSeconds(0);
    this.dateReferenceDST = new DateDST("France/Paris", dateNowArrondi, null);
    this.dateRechercheDST = new DateDST(null, null, null);
   }

  initialReference(aDateDST: DateDST) {
    this.dateReferenceDST = aDateDST;           
  }

  initialRecherche(aDateDST: DateDST) {
    this.dateRechercheDST = aDateDST;        
  }
  modificationReference(aDateDST: DateDST) {
    this.dateReferenceDST = aDateDST;
    this.calculHeureRecherche();            
  }

  modificationRecherche(aDateDST: DateDST) {
    this.dateRechercheDST = aDateDST;
    this.calculHeureRecherche()
  }

  calculHeureRecherche() {
    if ((this.dateReferenceDST.nomZone != undefined) && 
        (this.dateRechercheDST.nomZone != undefined) && 
        (this.dateReferenceDST.dateUTC != undefined)) {
          this.dateRechercheDST.dateUTC = this.dateReferenceDST.dateUTC;
          this.dateRechercheDST.dateLocale = null;
          console.log("calculHeureRecherche() : " + this.dateRechercheDST.dateUTC);
    };
  }

  inversion() {
    var dateTravailDST: DateDST = this.dateReferenceDST;
    this.dateReferenceDST = this.dateRechercheDST;
    this.dateRechercheDST = dateTravailDST;
  }

}
